html {
  line-height: 1.15;
  box-sizing: border-box;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.87);
}
nav {
  margin-bottom: 10px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  color: #fff;
  background-color: #ee6e73;
  width: 100%;
  height: 56px;
  line-height: 56px;
}
nav .nav-wrapper {
  position: relative;
  height: 100%;
}
nav .brand-logo {
  position: absolute;
  color: #fff;
  display: inline-block;
  font-size: 2.1rem;
  padding: 0;
}
nav img {
  height: 64px;
}
nav ul {
  margin: 0;
}
nav ul li {
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
  float: left;
  padding: 0;
}
nav ul a {
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
  font-size: 1rem;
  color: #fff;
  display: block;
  padding: 0 15px;
  cursor: pointer;
}
nav ul a:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
nav ul a.active {
  background-color: rgba(0, 0, 0, 0.1);
}
nav ul.left {
  float: left;
}
@media only screen and (min-width: 601px) {
  nav.nav-extended .nav-wrapper {
    min-height: 64px;
  }
  nav {
    height: 64px;
    line-height: 64px;
  }
  nav .nav-wrapper i {
    height: 64px;
    line-height: 64px;
  }
  nav a.sidenav-trigger {
    height: 64px;
    line-height: 64px;
  }
  nav a.sidenav-trigger i {
    height: 64px;
    line-height: 64px;
  }
  .navbar-fixed {
    height: 64px;
  }
}
